@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Lato";
  font-display: swap;
  src: url("../../fonts/lato/Lato-Regular.woff2") format("woff2"), url("../../fonts/lato/Lato-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Oswald";
  font-display: swap;
  src: url("../../fonts/oswald/oswaldmedium.woff2") format("woff2"), url("../../fonts/oswald/oswaldmedium.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Oswald";
  font-display: swap;
  src: url("../../fonts/oswald/oswaldregular.woff2") format("woff2"), url("../../fonts/oswald/oswaldregular.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  box-sizing: border-box;
  height: 100%;
}

body {
  min-width: 320px;
  height: 100%;
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Lato", sans-serif;
  color: #444444;
  background-color: #ffffff;
}

img {
  max-width: 100%;
  object-fit: cover;
}

* {
  outline: 1px solid rgb(179, 7, 7) !important;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.main {
  flex-grow: 1;
}

.page-wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100%;
  background-color: #ffffff;
}

.button {
  display: inline-block;
  padding: 10px;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  font-family: "Oswald", sans-serif;
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  background-color: #68b738;
  border: none;
  cursor: pointer;
}

.button:hover {
  background-color: #5eaa2f;
}

.button:focus {
  background-color: #5eaa2f;
}

.button:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #5eaa2f;
}

.container {
  width: 100%;
  max-width: 1260px;
  margin: 0 auto;
  padding: 0 20px;
}

.page-title {
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
  font-family: "Oswald", sans-serif;
  color: #000000;
}

.example {
  background-color: #eaeaea;
}

.example__title {
  margin-bottom: 40px;
}

.example__text {
  margin: 0;
  font-size: 14px;
  line-height: 18px;
}

.example__result-cover {
  display: grid;
  gap: 32px;
  grid-template-columns: repeat(2, 124px);
}

.example__result-item {
  padding: 14px 10px 13px;
  text-align: center;
  border: 1px solid #cdcdcd;
  border-radius: 3px;
}

.example__result-value {
  font-size: 24px;
  line-height: 24px;
  font-family: "Oswald", sans-serif;
  color: #000000;
  text-transform: uppercase;
}

.example__result-description {
  display: block;
  margin: 8px 8px -29px;
  padding: 0 5px;
  font-size: 12px;
  line-height: 12px;
  text-transform: lowercase;
  background-color: #eaeaea;
}

.example__price {
  font-size: 20px;
  line-height: 20px;
  font-family: "Oswald", sans-serif;
  text-align: center;
  color: #000000;
  text-transform: uppercase;
}

.logo-html {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 16px;
  line-height: 20px;
  color: #444444;
  text-decoration: none;
}
.logo-html svg {
  width: 27px;
  height: 34px;
}

.main-footer__social {
  width: 100%;
  margin-bottom: 20px;
  padding: 20px 0 19px;
  border-top: 1px solid #d9d9d9;
  border-bottom: 1px solid #d9d9d9;
}

.social {
  display: grid;
  justify-content: center;
  margin: 0;
  padding: 0;
  list-style: none;
  gap: 24px;
  grid-template-columns: repeat(3, auto);
}

.social__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 28px;
  height: 22px;
  color: #666666;
  fill: currentColor;
}

.main-header__container {
  position: relative;
  display: flex;
  justify-content: space-between;
}

.map {
  position: relative;
}

.map--home {
  background-color: #ffffff;
}

.map__contact {
  display: grid;
  padding: 26px 0;
  grid-template-columns: 1fr 1fr;
}

.map__title {
  margin: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  font-family: "Oswald", sans-serif;
  color: #111111;
  text-transform: uppercase;
}

.map__address {
  margin: 0;
  font-size: 14px;
  line-height: 20px;
}

.nav__toggle {
  position: absolute;
  top: 9px;
  right: 0;
  width: 24px;
  height: 24px;
  margin: 0;
  padding: 4px 0;
}

.nav--close .nav__list {
  display: none;
}

.nav__list {
  position: absolute;
  top: 100%;
  right: 0;
  left: 0;
  z-index: 1;
  display: flex;
  flex-direction: column;
  list-style: none;
  background-color: #ffffff;
}

.offer__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.offer__item {
  padding: 30px 20px 18px;
  background-color: #f2f2f2;
}

.offer__title {
  margin: 0;
  font-weight: 400;
  font-size: 24px;
  line-height: 37px;
  font-family: "Oswald", sans-serif;
  color: #000000;
  text-transform: uppercase;
}

.offer__text {
  margin: 0;
  font-size: 14px;
  line-height: 18px;
}

.offer__link {
  font-size: 16px;
  line-height: 16px;
  font-family: "Oswald", sans-serif;
  color: #000000;
  text-decoration: none;
}

.promo__description {
  margin: 0 -20px;
  color: #ffffff;
  background-color: rgba(104, 183, 56, 0.85);
  background-image: linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../img/index-background-mobile.jpg");
  background-repeat: repeat, no-repeat;
  background-position: top;
  background-size: auto, cover;
}

.promo__button {
  width: 100%;
}

.promo__title {
  margin-bottom: 25px;
  font-family: "Oswald", sans-serif;
  color: inherit;
}

.promo__text {
  margin: 0;
  font-size: 14px;
  line-height: 14px;
  font-family: "Oswald", sans-serif;
  text-transform: uppercase;
}

.site-list__link {
  display: block;
  padding: 23px 23px 22px;
  font-size: 20px;
  line-height: 20px;
  font-family: "Oswald", sans-serif;
  color: inherit;
  text-transform: uppercase;
  text-decoration: none;
}

.slider {
  position: relative;
  width: 100%;
  max-width: 280px;
  margin: 0 auto;
}

.slider__container {
  position: relative;
  min-height: 256px;
  overflow: hidden;
}

.slider__item {
  position: absolute;
  top: 0;
  left: 0;
  width: 50%;
  height: 100%;
  overflow: hidden;
}

.slider__item img {
  display: block;
  width: 100%;
  height: 100%;
}

.slider__item--before {
  z-index: 2;
}

.slider__item--after {
  right: 0;
  left: auto;
  z-index: 1;
}

.slider__item--before img {
  object-position: left;
}

.slider__item--after img {
  object-position: right;
}

.main-footer {
  background-color: #f2f2f2;
}

.main-footer__container {
  padding-top: 40px;
  padding-bottom: 40px;
}

.main-footer__social {
  width: 100%;
  margin-bottom: 20px;
  padding: 20px 0 19px;
  border-top: 1px solid #d9d9d9;
  border-bottom: 1px solid #d9d9d9;
}

.main-footer__logo {
  width: 128px;
  height: 24px;
  margin: 0 auto 19px;
}

.technology__title {
  margin-bottom: 40px;
}

.technology__list {
  display: grid;
  margin: 0;
  padding: 0;
  list-style: none;
  gap: 20px;
  grid-template-columns: 1fr;
}

.technology__item {
  position: relative;
  min-height: 60px;
  padding: 0 0 0 80px;
  font-size: 14px;
  line-height: 18px;
}

.main-header__container {
  position: relative;
  display: flex;
  justify-content: space-between;
}

.application__container {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding: 66px 0;
}

.application__title {
  margin: 0 0 55px;
  padding: 0;
  font-size: 60px;
  line-height: 60px;
  font-family: "Oswald", sans-serif;
}

.application__text {
  margin: 0;
  font-size: 20px;
  line-height: 30px;
  font-family: "Oswald", sans-serif;
  text-transform: uppercase;
}

.form__item {
  display: grid;
  column-gap: 13px;
  grid-template-columns: auto 1fr;
  row-gap: 20px;
}

.form__group--contacts {
  margin-bottom: 70px;
}

.list-contacts {
  display: grid;
  gap: 80px;
  grid-template-columns: auto auto;
}

.form-title-comments {
  margin-bottom: 78px;
}

.form-input-aria {
  width: 100%;
  min-width: 280px;
  min-height: 150px;
  padding: 9px 20px;
}
.form-input-aria placeholder {
  font-size: 20px;
  line-height: 30px;
  font-family: "Oswald", sans-serif;
  text-transform: uppercase;
}

.form__group--check {
  margin-bottom: 65px;
}

.form-title-check {
  margin-bottom: 62px;
}

.list-check {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  min-height: 50px;
}

.control__item-check {
  display: flex;
  flex-direction: row-reverse;
  margin-bottom: 65px;
}

.control__input--check {
  max-width: 40px;
  max-height: 40px;
}

.form__order {
  display: grid;
  margin-bottom: 79px;
  grid-template-columns: 1fr 1fr;
}

.application__wrapper {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding: 0;
}

.form__group-wrapper {
  display: flex;
  margin-bottom: 70px;
  gap: 80px;
}

.form__group {
  flex-grow: 1;
  padding: 0;
  border: none;
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

.form__title {
  margin-bottom: 76px;
  font-size: 36px;
  line-height: 36px;
  font-family: "Oswald", sans-serif;
  color: #000000;
}

.form__input {
  width: 100%;
  height: 52px;
  padding: 11px 20px;
  font-style: normal;
  font-size: 20px;
  font-family: "Lato", sans-serif;
  color: #444444;
  text-transform: uppercase;
  background-color: #ffffff;
  border: 1px solid #e7e7e7;
  appearance: none;
}
.form__input placeholder {
  font-size: 20px;
  line-height: normal;
  font-family: "Oswald", sans-serif;
  color: #444444;
  text-transform: uppercase;
}

.form-input[type=textarea] {
  appearance: textfield;
}

.form-input :hover {
  border-color: #cdcdcd;
}
.form-input :focus {
  border-color: #444444;
}
.form-input :error {
  border-color: #ff8282;
}

.form__label {
  font-weight: 400;
  font-size: 20px;
  line-height: normal;
  font-family: "Oswald", sans-serif;
  color: #444444;
  text-transform: uppercase;
  grid-column: 1/2;
}

.catalog__title {
  margin: 0;
  margin-bottom: 84px;
  padding: 0;
  font-size: 60px;
  line-height: 60px;
  font-family: "Oswald", sans-serif;
  color: #000000;
}

.catalog__list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 80px 0 76px;
  list-style: none;
  column-gap: 80px;
  row-gap: 63px;
}

.catalog__item {
  position: relative;
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  width: 17%;
  padding-top: 17px;
}
.catalog__item--more {
  display: flex;
  flex-grow: 1;
  padding: 50px 20px 40px;
}

.catalog__description {
  max-height: 430px;
  padding: 81px 38px;
  background-color: #f2f2f2;
}

.catalog__link-title {
  text-decoration: none;
}

.catalog__card-title {
  margin: 0 0 23px;
  font-size: 20px;
  line-height: 24px;
  font-family: "Oswald", sans-serif;
  text-align: center;
  color: #222222;
  text-transform: uppercase;
}

.catalog__link-image {
  display: block;
}

.catalog__image {
  position: absolute;
  top: -3%;
  left: 30%;
  z-index: 1;
  display: block;
}

.catalog__table {
  display: flex;
  flex-direction: column;
}

.catalog__table-item {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  margin: 0;
  margin-bottom: 10px;
  border-bottom: 1px solid #cdcdcd;
}

catalog__item--more {
  padding: 50px 20px 40px;
}

.catalog__card-title--more {
  margin: 0 0 26px;
  font-size: 20px;
  line-height: 24px;
  font-family: "Oswald", sans-serif;
  text-align: center;
  color: #111111;
  text-transform: uppercase;
}

.catalog__text--more {
  margin: 0 0 90px;
  line-height: 18px;
}

.button--more {
  color: #444444;
  background-color: #f2f2f2;
}
.button--more :hover {
  background-color: #ebebeb;
}
.button--more :focus {
  background-color: #969090;
}

.supplementary__container {
  display: flex;
  justify-content: space-between;
}

.supplementary__list {
  display: grid;
  list-style: none;
  grid-template-rows: repeat(4, 1fr);
}

.supplementary__item {
  display: grid;
  padding: 15px 0 17px;
  border-top: 1px solid #cdcdcd;
  border-bottom: 1px solid #cdcdcd;
  grid-template-columns: repeat(4, 1fr);
}

.supplementary__title {
  margin: 0;
  font-size: 20px;
  line-height: 20px;
  font-family: "Oswald", sans-serif;
  text-align: center;
  color: #222222;
  text-transform: uppercase;
  grid-column: 1/2;
  grid-template-columns: repeat(4, 1fr);
}

.supplementary__description {
  margin: 0;
  grid-column: 2/3;
}

.supplementary__price {
  margin: 0;
  grid-column: 3/4;
}

.supplementary__link {
  grid-column: 4/5;
}

.supplementary__proposal {
  min-width: 245px;
  min-height: 288px;
  padding: 185px 42px 43px;
  background-color: rgba(104, 183, 56, 0.85);
  background-image: linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../../img/cat-in-box.jpg");
  background-repeat: repeat, no-repeat;
  background-position: top;
  background-size: auto, cover;
}

.supplementary__text {
  margin: 0;
  line-height: 24px;
  text-align: center;
  color: #ffffff;
}